import { g } from './common'
/**
 * Chapter6 水电站压力管道水力学计算
 * 部分计算参考以下网址
 * https://raymondmcguire.github.io/?e=HYCAL&&theme=Image-Visual-Lab
 */
export module Chapter6 {

    /**
     * 6.1计算经济直径
     * @param v_e
     * @param Q_max
     * @returns D
     */
    export function jsjjzj(v_e:number, Q_max:number) {
      let D = Math.sqrt(4 * Q_max / (Math.PI * v_e))
      // console.log(D);
      return D
    }

    /**
     * 6.2 圆形管道水头损失计算
     * @param D
     * @param Q
     * @param n
     * @param zeta
     * @param L
     * @returns h
     */
    export function yxgdssjs(D:number, Q:number, n:number, zeta:number, L:number) {
      let R = D / 4
      let v = 4 * Q / (Math.PI * D * D)
      let h_y = n * n * v * v * L / Math.pow(R, 4 / 3)
      let h_j = zeta * v * v / (2 * g)
      // console.log(R);
      // console.log(zeta);
      // console.log(v);
      // console.log(h_y, h_j)
      let h = h_j + h_y
      // console.log(h);
      return h
    }

    /**
     * 6.3 水锤计算
     * 6.3.1 求水锤波速C
     * @param D
     * @param E_s
     * @param delta_s
     * @param E_w
     * @param gamma
     */
    export function scbs(D:number, E_s:number, delta_s:number, E_w:number, gamma:number) {
      let r = D / 2
      let K = E_s * delta_s / (r * r)
      // console.log(K);
      let C = Math.sqrt(E_w * g * 100 / gamma) / (Math.sqrt(1 + 2 * E_w / (K * r)) * 100)
      return C
    }

    /**
     * 6.3.2 管道特征系数
     * @param C
     * @param v_max
     * @param H_0
     */
    export function gdtzxs(C:number, v_max:number, H_0:number) {
      let rou = C * v_max / (2 * g * H_0)
      // console.log(rou);
      return rou
    }

    /**
     * 6.3.3 求水锤的特性参数
     * @param L
     * @param v_max
     * @param T_s
     * @param H_0
     */
    export function sctxcs(L:number, v_max:number, T_s:number, H_0:number) {
      let sigma = L * v_max / (g * H_0 * T_s)
      // console.log(sigma);
      return sigma
    }

    /**
     * 6.3.4 判断直接水锤和间接水锤
     * @param L
     * @param C
     * @param T_s
     */
    export function pdzjschjjsc(L:number, C:number, T_s:number) {
      let t_r = 2 * L / C
      // console.log(t_r);
      if (T_s < t_r) {
        return '直接水锤'
      } else {
        return '间接水锤'
      }
    }

    /**
     * 6.3.5 直接水锤计算
     * @param C
     * @param T_s
     */
    export function zjsc(C:number, v_0:number) {
      let delta_H = C * v_0 / g
      // console.log(delta_H);
      return delta_H
    }

    /**
     * 6.3.6 间接水锤计算
     * @param tou_0
     * @param rou
     * @param sigma
     * @param H_0
     */
    export function jssc(tou_0:number, rou:number, sigma:number, H_0:number) {
      let judge = tou_0 * rou
      let tou_1 = 0
      let eta_1 = 0

      if (judge < 1) {
        // 第一相水锤
        tou_1 = 2 * sigma / (1 + judge - sigma)
        eta_1 = 2 * sigma / (1 + judge + sigma)
      } else {
        tou_1 = 2 * sigma / (2 - sigma)
        eta_1 = 2 * sigma / (2 + sigma)
      }
      let delta_H = tou_1 * H_0
      let minus_delta_H = eta_1 * H_0

      // console.log(delta_H);
      // console.log(minus_delta_H);

      return [delta_H, minus_delta_H]
    }
}
